@import url('https://fonts.googleapis.com/css2?family=Sora&display=swap');

* {
  font-family: 'Lato', sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.color-overlay {
  position: absolute;
  background-color: rgba(0,0,0,0.2);
  height: 100%;
  width: 100%;
  mix-blend-mode: normal;
}


form {
  background-color: white;
}

.textStyle{
  color: #191F24;
  font-family: Lato,sans-serif;
  font-size: 23px;
  line-height: 35px;
  text-align: left;
  padding: 40px;
  font-weight: bold;
}

